<template>
<div>




     <zwRow>

        <zwCol class="pull-right" span=24>
              <zwJumbotron v-on:clickFn="scanCode" title=""></zwJumbotron>
        </zwCol>

    </zwRow>
    <zwRow>
      <zwCol style="padding-left:20px">

              <zwRadioGroup v-on:change="changeModel" :options="options">
              </zwRadioGroup>

        </zwCol>
    </zwRow>
    <zwRow>
        <zwCol>
           <zwCollapse >
                          <zwPanel state="open">
                          <span slot="title" name="title"></span>


                            <ul slot="body" name="body">
                              <li  v-for="result in list" key={{result.deviceid}} >
                              <zwSlidPanel v-if="result.status==3"  :canDel="result.deviceSubTypeCode=='010'?true:false" v-on:deleteCallBack="deleteDevice(1,result.wifiId)" :id="result.deviceid?result.deviceId:result.wifiId" :imgSrc="getDeviceImg(result.deviceSubTypeCode,result.deviceSubTypeName,result.online)" :link="getDeviceViewLink(result.deviceSubTypeCode,result.deviceid,result.wifiId)"  :title="result.deviceSubTypeName" :subTitle="':'+result.online==1?'':''" :thrdTitle="':'+result.deviceSubTypeCode">

                              <img :src="getDeviceImg(result.deviceSubTypeCode,result.deviceSubTypeName,result.online)" slot="img"></img>

                               <img :src="getSwitchImg()" slot="switch"></img>
                              </zwSlidPanel>
                              </li>
                          </ul>

                          </zwPanel>
                          <zwPanel state="open" >
                          <span slot="title" name="title"></span>
                              <ul slot="body" name="body">
                                 <li  v-for="result in list" key={{result.id}} >
                                 <zwSlidPanel v-if="!result.online || result.online==1" :canDel="result.deviceSubTypeCode=='010'?true:false" v-on:deleteCallBack="deleteDevice" :id="(result.deviceid?result.deviceid:result.wifiId)"  :imgSrc="getDeviceImg(result.deviceSubTypeCode,result.deviceSubTypeName,result.online)" :link="getDeviceViewLink(result.deviceSubTypeCode,result.deviceid,result.wifiId)" :title="result.deviceSubTypeName" :subTitle="':'+(result.online==1?'':'')" :thrdTitle="':'+result.deviceSubTypeCode">

                                 <img :src="getDeviceImg(result.deviceSubTypeCocde,result.deviceSubTypeName,result.online)" slot="img"></img>

                                  <img :src="getSwitchImg()" slot="switch"></img>
                                 </zwSlidPanel>
                                 </li>
                             </ul>
                          </zwPanel>
                          <zwPanel  state="open">
                          <span slot="title" name="title"></span>
                              <ul slot="body" name="body">
                                 <li  v-if="result.online==0" v-for="result in list" key={{result.id}} >

                                 <zwSlidPanel  :canDel="result.deviceSubTypeCode=='010'?true:false" v-on:deleteCallBack="deleteDevice" :id="result.deviceid?result.deviceId:result.wifiId" :imgSrc="getDeviceImg(result.deviceSubTypeCode,result.deviceSubTypeName,result.online)"  :link="getDeviceViewLink(result.deviceSubTypeCode,result.deviceid,result.wifiId)" :title="result.deviceSubTypeName" :subTitle="':'+(result.online==1?'':'')" :thrdTitle="':'+result.deviceSubTypeCode">

                                 <img :src="getDeviceImg(result.deviceSubTypeCode,result.deviceSubTypeName,result.online)" slot="img"></img>

                                  <img :src="getOffLineSwitchImg()" slot="switch"></img>
                                 </zwSlidPanel>
                                 </li>
                             </ul>
                          </zwPanel>

                      </zwCollapse>
                      <div style="height:100px">
                      </div>
          </zwCol>
          </zwRow>
          <zwRow>
          <zwCol>
            <zwAppImgList :list="imgList"></zwAppImgList>
          </zwCol>
          </zwRow>
  <zwRow class="fix-bottom">
 <zwCol  span=24>
    <bottomBar  defaultIndex="1"></bottomBar>
 </zwCol>
  </zwRow>


</div>

</template>
<script>

import zwRow from '../../component/layout/zwRow.vue';
import zwCol from '../../component/layout/zwCol.vue';
import zwIcon from '../../component/icon/zwIcon.vue';



import zwPcImgList from '../../component/datadisplay/zwPcImgList.vue';
import zwAppImgList from '../../component/datadisplay/zwAppImgList.vue';
  import zwJumbotron from '../../component/datadisplay/zwJumbotron.vue';
  import zwSlidPanel from '../../component/datadisplay/zwSlidPanel.vue';
  import zwCollapse from '../../component/datadisplay/zwCollapse.vue';
import zwPanel from '../../component/datadisplay/zwPanel.vue';
import bottomBar from '../smarthome/bottomBar.vue';
 import zwRadioButton from '../../component/datadisplay/zwRadioButton.vue';
 import zwRadioGroup from '../../component/datadisplay/zwRadioGroup.vue';
 import zwMessage from '../../component/feedback/zwMessage.vue';
export default {
    components:{zwRow,zwCol,zwIcon,bottomBar,zwJumbotron,zwSlidPanel,zwPanel,zwCollapse,zwMessage,
    zwRadioGroup,zwRadioButton,zwPcImgList,zwAppImgList
    },
    data () {

        return {
        list:[],
        imgList:[

                { id:1,title:"()",tag1:["","",""],tag2:["80","888","88"],tag3:["","","",""],score:"5.5",commentCount:"12",price:"1231",subtitle:"1010101401",pic:"static/img/carousel/11.png"},
                 {id:1,title:"()",tag1:["","",""],tag2:["80","888","88"],tag3:["","","",""],score:"5.5",commentCount:"12",price:"1231",subtitle:"1010101401",pic:"static/img/carousel/11.png"},
                 {id:1,title:"()",tag1:["","",""],tag2:["80","888","88"],tag3:["","","",""],score:"5.5",commentCount:"12",price:"1231",subtitle:"1010101401",pic:"static/img/carousel/11.png"},
                 {id:1,title:"()",tag1:["","",""],tag2:["80","888","88"],tag3:["","","",""],score:"5.5",commentCount:"12",price:"1231",subtitle:"1010101401",pic:"static/img/carousel/11.png"},
                 {id:1,title:"()",tag1:["","",""],tag2:["80","888","88"],tag3:["","","",""],score:"5.5",commentCount:"12",price:"1231",subtitle:"1010101401",pic:"static/img/carousel/11.png"}

                ],
        tabIndex:2,
        options:[
        {  value:0,name:"" },
        {  value:1,name:"" },

        ]
      }
    }
    ,
    mounted(){
      this.getDeviceList();

    },
    computed: {
        getWangluoguanliImg:function(){
            return PATH+ (this.tabIndex==1?'/static/img/smarthome/2.png':'/static/img/smarthome/1.png');
        },

        getZhinengjiatingImg:function(){
            return PATH+ (this.tabIndex==2?'/static/img/smarthome/2.png':'/static/img/smarthome/1.png');
        },
        getGerenzhongxinImg:function(){
            return PATH+ (this.tabIndex==3?'/static/img/smarthome/2.png':'/static/img/smarthome/1.png');
        },
    },
    methods:{
    getDeviceList:function(){

      Ajax.getJSON(PATH+"/smart/home/device/listdb",null,function(result){
         if(result.r==AJAX_SUCC){

             this.list=result.data;
             //alert(this.list.length);
         }
        }.Apply(this));
    },
    initWx:function(data){


    //https://mp.weixin.qq.com/wiki?t=resource/res_main&id=mp1421141115
    //https://mp.weixin.qq.com/wiki?t=resource/res_main&id=mp1421141115
    //JS

    //config
   //alert(data.signature);

    wx.config({

        debug: false, // ,apialertpclogpc

        appId: data.appId, // 

        timestamp: data.timestamp, // 

        nonceStr: data.noncestr, // 

        signature:data.signature,// 1

        jsApiList: [ 'getLocation','scanQRCode'] // JSJS2

    });
    //ready

    wx.ready(function(){
        wxInit=true;
        console.log("ready");
       // alert("ready");
        // configreadyconfigconfigreadyready

          if(wx){
                     wx.scanQRCode({//
                        needResult: 1, // 01
                        scanType: ["qrCode","barCode"], // 
                        success: function (res) {
                            var result = res.resultStr; // needResult  1 
                           // alert(""+result);
                            this.addWifi(result);
                         }.Apply(this)
                    });
                    }else{
                    alert("!");
                    }

    }.Apply(this));
    //error

    wx.error(function(res){
        alert("");
        // configerrorconfigdebugresSPA

    });


    },
    scanCode:function(){//alert("scanCode")


        if(!wxInit){//wx 

        Ajax.getJSON(PATH+"/weixin/signatrue",{url:location.href.split('#')[0]},function(result){
                  var data = result.data;
                  this.initWx(data);


              }.Apply(this));
        }else{
           // this.addWifi("86100c00a0050010000000054d20c674");

                if(wx){
                     wx.scanQRCode({//
                        needResult: 1, // 01
                        scanType: ["qrCode","barCode"], // 
                        success: function (res) {

                            var result = res.resultStr; // needResult  1 
                           // alert(""+result);
                            this.addWifi(result);
                         }.Apply(this)
                    });
                    }else{
                    alert("!");
                    }
        }
    },
    addWifi:function(result){
        Ajax.post(PATH+"/smart/home/device/add",{wifiId:result},function(result){
            // alert("ajax"+result[AJAX_RESULT] );
            ajaxResultHandler(result);

           //  alert("ajaxResultHandler");
            //alert(result[AJAX_RESULT] );
            if(result[AJAX_RESULT] == AJAX_SUCC){//alert("" );
              zalert("!");
            //this.list=[];
                this.getDeviceList();
            }else{//alert("" );
                alert(result.msg);
            }
        }.Apply(this)
       );
    },
    getSwitchImg:function(){
        return PATH+"/static/img/smarthome/.png";
    },
    getOffLineSwitchImg:function(){
         return PATH+"/static/img/smarthome/.png";
    },
    getDeviceViewLink:function(deviceSubCode,deviceId,wifiId){
        var link ="";
      if(deviceSubCode=='010'){//deviceSubName==""
        link+="#deviceGateWayView";
        }else if(deviceSubCode=='046' ){//deviceSubName==""
        link+="#deviceLockView";
        }else if(deviceSubCode=='002'){//deviceSubName==""
        link+="#deviceWaterView";
        }else if(deviceSubCode=='001'){//deviceSubName==""
        link+="#deviceRedView";
        }else if(deviceSubCode=='003'){//deviceSubName==""
        link+="#deviceMockView";
        }else if(deviceSubCode=="011"){
         link+="#deviceSocketView";
        }
        link+="?deviceId="+deviceId+"&wifiId="+wifiId;
        return  link;
    },
        deleteDevice:function(deviceId){

          Ajax.del(PATH+"/smart/home/device/"+deviceId,{},function(result){
            if(result.r==AJAX_SUCC){
                alert("");
                this.list=[];
                this.getDeviceList();
            }else{
                alert(result.msg);
            }
          }.Apply(this));
        },
        getDeviceImg:function(deviceSubCode,deviceSubName,Online){
            var imgpath =PATH+ "/static/img/smarthome/";
            console.log(deviceSubCode);
          if(deviceSubCode=='010'){//deviceSubName==""
             imgpath+="";
          }else if(deviceSubCode=='046' ){//deviceSubName==""
             imgpath+="";
          }else if(deviceSubName==""){//deviceSubName==""
            imgpath+="";
         }else if(deviceSubCode=='002'){//deviceSubName==""
             imgpath+="";
        }else if(deviceSubCode=='001'){//deviceSubName==""
            imgpath+="";
        }else if(deviceSubCode=='003'){//deviceSubName==""
             imgpath+="";
         }else if(deviceSubCode=="011"){
            imgpath+="";
         }
         if(Online==1){
            imgpath+="3";
         }else{
               imgpath+="4";
         }
         imgpath+=".png";
         return imgpath;



        },
        changeModel:function(value){
             var wifiId =null;
                        var redDeviceId =null;
                        var socketDeviceId =null;
           for(var i=0;i<this.list.length;i++){
                           wifiId = this.list[i].wifiId;
                           if(this.list[i].deviceSubTypeCode=="001"){
                                   redDeviceId= this.list[i].deviceid;
                           }
                            if(this.list[i].deviceSubTypeCode=="011"){
                                   socketDeviceId= this.list[i].deviceid;
                           }
                       }
                        if(!wifiId){
                            ztips("!");
                            return;

                        }

                          Ajax.put(PATH+"/smart/home/device/modleRemoteCmd",{
                                                    "wifiId":wifiId,
                                                    "model":value,
                            },function(result){
                                if(result.r==AJAX_SUCC){
                                   ztips("!",1000);

                                }else{
                                    alert(result.msg);
                                }
                            }.Apply(this));


           /* if(value==1){
                //
                //  


                 Ajax.post(PATH+"/smart/home/device/modleRemoteCmd",{
                            "wifiId":wifiId,
                            "deviceId":redDeviceId,
                            "turn":0

                            },function(result){
                                if(result.r==AJAX_SUCC){
                                   ztips("!",1000);

                                }else{
                                    alert(result.msg);
                                }
                            }.Apply(this));

                Ajax.post(PATH+"/smart/home/device/plugRemoteCmd",{
                                "wifiId":wifiId,
                                "deviceId":socketDeviceId,
                                "power":1

                            },function(result){
                                if(result.r==AJAX_SUCC){
                                   ztips("");

                                }else{
                                    alert(result.msg);
                                }
                            }.Apply(this));
            }else if(value==2){
                //
                // 
                 Ajax.post(PATH+"/smart/home/device/infraRedRemoteCmd",{
                    "wifiId":wifiId,
                    "deviceId":redDeviceId,
                    "turn":1

                    },function(result){
                        if(result.r==AJAX_SUCC){
                           ztips("!",1000);

                        }else{
                            alert(result.msg);
                        }
                    }.Apply(this));


                  Ajax.post(PATH+"/smart/home/device/plugRemoteCmd",{
                                                "wifiId":wifiId,
                                                "deviceId":socketDeviceId,
                                                "power":0

                                            },function(result){
                                                if(result.r==AJAX_SUCC){
                                                   ztips("");

                                                }else{
                                                    alert(result.msg);
                                                }
                                            }.Apply(this));

            }*/
        },
        selectTab:function(index){

            this.tabIndex=index;
            if(index==2){
                 window.location="#userCenter";
            }
        }
    }
}
</script>
<style scoped>



</style>




